<template>
  <div class="hamburger-menu">
    <div class="burger">
      <span class="span__menu" v-show="check===false" @click="checked"></span>
      <span class="span__exit" v-show="check===true" @click="checkedOut"></span>
    </div>
    <div class="bg__shadow" v-show="check" @click="checkedOut"></div>
      <ul class="menu__box" v-show="check">
        <li><a
            class="menu__item"
            @click="$router.push('/'), checkedOut()"
        > Main
        </a></li>
        <li><a
            class="menu__item"
            @click="$router.push('/calculate'), checkedOut()"
        >
          Calculator
        </a></li>
        <li><a
            class="menu__item"
            @click="$router.push('/contacts'), checkedOut()"
        >
          Contacts
        </a></li>
      </ul>
  </div>
</template>

<script>
export default {
  data(){
    return{
      check: false
    }
  },
  methods: {
    checked() {
      this.check = true
    },
    checkedOut() {
      this.check = false
    }
  }
}
</script>

<style scoped>
.hamburger-menu{
  margin-bottom: 40px;
  display: none;
}

.burger {
  position: absolute;
  top:35px;
  left:29px;
}

.span__menu,
.span__menu::before,
.span__menu::after {
  display: block;
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #15FF01;
}
.span__menu::before {
  content: '';
  top: -8px;
}
.span__menu::after {
  content: '';
  top: 8px;
}

.span__exit::before,
.span__exit::after {
  content: '';
  display: block;
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #15FF01;
  z-index: 3;
}
.span__exit::before {
  content: '';
  transform: rotate(45deg);
}
.span__exit::after {
  content: '';
  transform: rotate(-45deg);
}

.menu__box {
  position: absolute;
  list-style: none;
  top: 0px;
  left: 0px;
  padding-top: 100px;
  padding-right: 20px;
  margin: 0;
  color: #15FF01;
  background: black;
  text-align: center;
  width: 150px;
  height: 100%;
  z-index: 1;
  border: #15FF01 1px solid;
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.5);
}

.menu__item{
  display: block;
  padding: 20px;
  font-size: 20px;
}
.menu__item:hover{
  background: #092B00;
}
.bg__shadow {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

@media
(max-width: 540px){
  .hamburger-menu {
    display: block;
  }
