<template>
  <div class="main">
    <a class="head" href="#footer">RUBIKORP</a>
    <start-game
        v-if="!this.checkStart"
        @click="touchStart()"
    />
    <my-profile
        v-else="this.checkStart"
        @exit = "mainExit"
    />
    <footer id="footer">
      <link-contacts/>
    </footer>
  </div>
</template>

<script>
import StartGame from '@/components/StartGame';
import LinkContacts from "@/components/LinkContacts";
import MyProfile from "@/components/MyProfile";


export default {

  data() {
    return {
      checkStart: false,
    }
  },
  components: {    StartGame,
    LinkContacts,
    MyProfile
  },
  methods: {
    touchStart() {
      this.checkStart = true;
    },
    mainExit() {
      this.checkStart = false;
    }
  },
}
</script>

