import { createRouter, createWebHistory } from 'vue-router'
import Contacts from '../views/MainMenu.vue'
import CalculateMain from "@/views/CalculateMain";
import AboutMe from "@/views/AboutMe";

const routes = [
  {
    path: '/',
    name: 'about-me',
    component: AboutMe
  },
  {
    path: '/calculate',
    name: 'calculate',
    component: CalculateMain
  },
  {
    path: '/contacts',
    name: 'contacts',
    component: Contacts
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
